<template>
  <div class="register">
    <mt-header>
      <router-link :to="{path:'/'}" slot="left">
        <mt-button icon="back">注册</mt-button>
      </router-link>
    </mt-header>
    <div class="registerBody">
    <a class="field" style="margin-top:10%;" :style="{borderBottom: phoneborderBottom}">
      <div class="wraper">
        <div class="value">
          <span>+86</span>
          <span style="margin-right:10px;margin-left:2px;"> | </span>
          <input v-model="username" @focus="focusInput('phoneborderBottom')" @blur="blurInput('phoneborderBottom')" placeholder="输入手机号" type="text" class="core">
        </div>
      </div>
    </a>
    <a class="field diff" style="margin-top:10%;" :style="{borderBottom: authborderBottom}">
      <div class="wraper">
        <div class="value">
          <input v-model="authCode" @focus="focusInput('authborderBottom')" @blur="blurInput('authborderBottom')" placeholder="输入验证码" type="text" class="core">
        </div>
        <mt-button class="authBtn" @click.native="getAuthCode" type="primary">获取验证码</mt-button>
      </div>
    </a>
    <a class="field" :style="{borderBottom: passwdBottom}">
        <div class="img"><img :src="passwdSrc" alt=""></div>
        <div class="wraper">
          <div class="value">
            <input v-model="password" v-if="show" @focus="passwdFocus" @blur="passwdBlur" placeholder="设置6-18位的登录密码" type="text" class="core">
            <input v-model="password" v-else @focus="passwdFocus" @blur="passwdBlur" placeholder="请输入6-18位的重置密码" type="password" class="core">
          </div>
        </div>
        <div class="img" @click="showPasswd" style="margin-right:5%;"><img :src="passwdEyeSrc" alt=""></div>
      </a>
      <div class="checklist">
        <a class="c_cell">
          <div class="c_wraper">
            <div class="c_title">
              <label class="c_label">
                <span class="c_checkbox">
                  <input type="checkbox" class="c_input" value="已同意">
                  <span class="c_core">
                  </span>
                </span>
                <span class="c_c_label">
                  已同意
                  <span @click="protocol">《***用户协议》</span>
                </span>
              </label>
            </div>
          </div>
        </a>
      </div>
      <mt-button class="btn" @click.native="finish" type="primary" size="large">完成</mt-button>
    </div>
  </div>  
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import { Toast } from 'mint-ui';
export default {
  name: 'forgetPasswd',
  data () {
    return {
      username: '',
      authCode: '',
      password: '',
      agree: [],
      phoneborderBottom: '',
      authborderBottom: '',
      passwdSrc: '/static/passwd.png',
      passwdEyeSrc: '/static/passwdHide.png',
      show: false,
      passwdBottom: ''
    }
  },
  methods: {
    ...mapActions({
      getAuthCodeFn: 'getAuthCode',
      validateAuthCode: 'validateAuthCode',
      register: 'register',
      changeToken: 'changeToken',
      changeAvatar: 'changeAvatar',
      changePhone: 'changePhone'
    }),
    focusInput: function(name) {
      console.log(name);
      this[name] = '2px solid #d06091';
    },
    blurInput: function(name) {
      this[name] = '1px solid #e3dee1';
    },
    passwdFocus: function() {
        this.passwdSrc = '/static/passwdNow.png';
        this.passwdBottom = '2px solid #d06091'
    },
    passwdBlur: function() {
      this.passwdSrc = '/static/passwd.png';
      this.passwdBottom = '1px solid #e3dee1';
    },
    showPasswd: function() {
      this.passwdSrc = '/static/passwdNow.png';
      if(this.passwdEyeSrc == '/static/passwdHide.png') {
        this.passwdEyeSrc ='/static/passwdShow.png';
        this.show = true;
      } else {
        this.passwdEyeSrc ='/static/passwdHide.png';
        this.show = false;
      }
    },
    getAuthCode: function() {
      let data = {};
      data.action = 'signUp';
      data.mobile = this.username;
      this.getAuthCodeFn(data).then(response => {

      },error => {
        if(error.body.code == 5001) {
            Toast({
              message: '验证码类短信1小时内同一手机号发送次数不能超过3次',
              position: 'bottom',
              duration: 2000
            });
          }
      });
    },
    finish: function() {
      console.log(this.agree[0]);
      if(!this.agree[0]) {
        Toast({
          message: '请阅读并同意用户协议',
          position: 'bottom',
          duration: 1000000000
        });
      } else {
        let data = {};
        data.action = 'signUp';
        data.mobile = this.username;
        data.authCode = this.authCode;
        this.validateAuthCode(data).then(response => {
          console.log(response.data,2112);
          let data = {};
          data.username = this.username;
          data.password = this.password;
          data.roleId = 6;
          this.register(data).then(response => {
            this.changeToken(response.data.token);
            this.changeAvatar(response.data.avatar);
            this.changePhone(response.data.phone);
            this.$router.push({path: '/index'});
          },error => {
            if(error.body.code == 2001) {
              Toast({
                message: '用户已存在',
                position: 'bottom',
                duration: 2000
              });
            }
          })
        }, error => {
          if(error.body.code == 5002) {
            Toast({
                message: '验证码无效',
                position: 'bottom',
                duration: 2000
              });
          }
        })
      }
    },
    protocol: function() {
      this.$router.push('/protocol');
    }
  },
  created() {
    this.agreeOption = ['已同意']
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.register .mint-cell-text {
  font-size: 0.5rem;
}
.authCode {
  display: flex;
  justify-content: space-between;
}
.protocol {
  display: flex;
}
.register .mint-header {
  background-color: #fff;
  color: #000000;
}
.register .mint-header label {
  margin-left: 2%;
}
.register .registerBody {
  padding: 2%;
}
.register .mint-button.btn{
  margin-top: 20%;
  background-color: #fff; 
  border-radius: 8px;
  border: 1px solid #dad2da;
  color: #d06091;
}
.register .mint-button::after {
  background-color: #f1cfde;
}
.register .authBtn {
  box-shadow: none;
  background-color: #fff;
  border: 1px solid #d06091;
  color: #d06091;
}
.c_cell {
  position: relative;
  background-color: #fff;
  box-sizing: border-box;
  color: inherit;
  min-height: 48px;
  display: block;
  overflow: hidden;
}
.checklist {
  margin-top: 2%;
}
.c_wraper {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.4rem;
  line-height: 1;
  min-height: inherit;
  overflow: hidden;
  padding: 0 10px;
  position: relative;
  width: 100%;
}
.c_title {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.c_label {
  display: block;
  padding: 0 10px;
}
.c_input {
  display: none;
}
.c_core {
  display: inline-block;
  background-color: #fff;
  border-radius: 100%;
  border: 1px solid #ccc;
  position: relative;
  width: 20px;
  height: 20px;
  vertical-align: middle;
}
.c_core::after {
  border: 2px solid transparent;
  border-left: 0;
  border-top: 0;
  content: " ";
  top: 3px;
  left: 6px;
  position: absolute;
  width: 4px;
  height: 8px;
  -webkit-transform: rotate(45deg) scale(0);
  transform: rotate(45deg) scale(0);
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
.c_c_label {
  vertical-align: middle;
  margin-left: 6px;
  color: #dad2da;
}
.c_input:checked + .c_core {
  background-color: #d06091;
  border-color: #efeeef;
}
.c_input:checked + .c_c_label {
  color: #d06091;
}
/*.c_input:checked + .c_core::after {
  border-color: #fff;
  -webkit-transform: rotate(45deg) scale(1);
  transform: rotate(45deg) scale(1);
}*/
.register .mint-header{
  font-size: 0.4rem;
  height: 1rem;
  line-height: 1rem;
}
.mintui {
  font-size: 0.4rem;
}
.register .mint-cell-text {
  font-size: 0.4rem;
}
.register .mint-button-text {
  font-size: 0.4rem;
}
.register .mint-button {
  height: 100%;
}
</style>



